<template>
  <div class="pageContent">
    <el-card class="box-card">
      <div class="pageHead">门店信息</div>
      <div>
        <el-form :model="ruleForm"  ref="ruleForm" label-width="160px" class="demo-ruleForm">
          <el-form-item label="自提点名称：" required>
            <el-input style="width:400px;" v-model.trim="ruleForm.name" maxlength="20" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="联系电话：" required>
            <el-input style="width:400px;" v-model.trim="ruleForm.phoneNumber" maxlength="11" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="自提点地址：" required>
            <div class="flex-row-start">
              <el-form-item style="margin-right:10px;">
                <el-select @change="address1" value-key="code" v-model="ruleForm.province" placeholder="请选择省">
                  <el-option v-for="item in addressList1" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item style="margin-right:10px;">
                <el-select @change="address2" value-key="code" v-model="ruleForm.city" placeholder="请选择市">
                  <el-option v-for="item in addressList2" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="ruleForm.district" value-key="code" placeholder="请选择区">
                  <el-option v-for="item in addressList3" :key="item.code" :label="item.name" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-form-item>
          <el-form-item label="自提点详细地址：" required>
            <el-input type="textarea" style="width:400px;" v-model.trim="ruleForm.address" maxlength="50" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="自提点门店导航：" required>
            <div class="mapBox amap-page-container">
              <div  :style="{width:'100%',height:'400px'}">
                <el-amap-search-box 
                  class="search-box" 
                  :search-option="searchOption" 
                  :on-search-result="onSearchResult"></el-amap-search-box>
                <el-amap 
                  ref="map" 
                  vid="amap" 
                  :plugin="plugin" 
                  class="amap-demo" 
                  :center="center"
                  :events="events"
                >
                  <el-amap-text 
                    v-for="(text,index) in markers" :key="'text'+index" 
                    :text="text.text" 
                    :offset="text.offset" 
                    :position="text.position"
                  ></el-amap-text>
                  <el-amap-marker 
                    v-for="(marker,index) in markers" :key="'marker'+index"
                    :position="marker.position" 
                    :events="marker.events"
                  ></el-amap-marker>
                </el-amap>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="自提时间：">
            <el-checkbox 
              v-model="ruleForm.isSelectSelffetchTime"
              true-label="1"
              false-label="2"
            >需要卖家选择自提时间</el-checkbox>
            <div>勾选后,买家下单选择上门自提,必须选择自提时间,卖家需要按约定时间备货</div>
            <div>不勾选,将会提示卖家尽快到店自提</div>
          </el-form-item>
          <el-form-item v-if="ruleForm.isSelectSelffetchTime == 1" label="自提时段：" required>
            <el-button type="primary" :disabled="selfeditNow" @click="selfaddTimer">添加自提时段</el-button>
            <div class="timePick" v-for="(item,index) in ruleForm.selffetchTimeJson" :key="index">
              <el-time-picker
                :disabled="item.type == 'finish'"
                is-range
                v-model="item.times"
                value-format="HH:mm"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                placeholder="选择时间范围"
                :picker-options="{
                  format: 'HH:mm'
                }"
              >
              </el-time-picker>
              <el-select :disabled="item.type == 'finish'" style="width:350px;" v-model="item.selectDay" multiple placeholder="请选择">
                <el-option
                  v-for="item in selfdays"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disabled">
                </el-option>
              </el-select>
              <el-button v-if="item.type == 'edit'" type="primary" @click="selfgetTimerData(item)">确定</el-button>
              <el-button @click="selfeditTimerData(item,index)">删除</el-button>
            </div>
          </el-form-item>
          <el-form-item v-if="ruleForm.isSelectSelffetchTime == 1" label="预约自提：" required>
            <div>
              <el-radio-group v-model="ruleForm.moveUpState">
                <div class="mt13">
                  <el-radio :label="1">无需提前</el-radio>
                </div>
                <div class="mt20">
                  <el-radio :label="2">提前&nbsp; 
                    <el-input-number
                      v-model="ruleForm.moveUpDayNum" 
                      :controls="false"
                      step-strictly
                      :min="1" 
                      :max="31"
                      placeholder="仅可输入1-31"
                    ></el-input-number> 天预约。为自然天,提前1天,买家则预约第二天自提。
                  </el-radio>
                </div>
                <div class="mt20">
                  <el-radio :label="3">提前&nbsp; 
                    <el-input-number
                      v-model="ruleForm.moveUpHourNum" 
                      :controls="false"
                      step-strictly
                      :min="1" 
                      :max="48"
                      placeholder="仅可输入1-48"
                    ></el-input-number> 小时预约。提前1小时,买家则预约60分钟后时段自提。
                  </el-radio>
                </div>
              </el-radio-group>
            </div>
          </el-form-item>
          <el-form-item v-if="ruleForm.isSelectSelffetchTime == 1" label="预约天时：" required>
            <span>最长可预约 </span>
            <el-input-number
              v-model="ruleForm.maxMoveUpNum" 
              :controls="false"
              step-strictly
              :min="1" 
              :max="31"
              placeholder="仅可输入1-31"></el-input-number>
              <span> 天内订单。建议最长预约时间大于提前预约时间一周以上，避免用户无法下单。</span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="subLoading" @click="onSubmit">立即创建</el-button>
            <el-button @click="backPage">取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getAddressLevel } from '@/api/shop/createShop'
import { addShopSelffetch } from '@/api/logistics/selfPickUp'
export default {
  data(){
    const self = this;
    return{
      subLoading:false,
      checkDays:[],
      days:[
        {
          value:'0',
          label:'周一',
          disabled:false
        },
        {
          value:'1',
          label:'周二',
          disabled:false
        },
        {
          value:'2',
          label:'周三',
          disabled:false
        },
        {
          value:'3',
          label:'周四',
          disabled:false
        },
        {
          value:'4',
          label:'周五',
          disabled:false
        },
        {
          value:'5',
          label:'周六',
          disabled:false
        },
        {
          value:'6',
          label:'周日',
          disabled:false
        }
      ],
      center: [121.59996, 31.197646],
      searchOption: {
        city: '',
        citylimit: false
      },
      lng: 0,
      lat: 0,
      trueMaker: false,
      plugin: [
        {
          enableHighAccuracy: true,//是否使用高精度定位，默认:true
          timeout: 100,            //超过10秒后停止定位，默认：无穷大
          maximumAge: 0,           //定位结果缓存0毫秒，默认：0
          convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true,        //显示定位按钮，默认：true
          buttonPosition: 'RB',    //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true,     //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy:true,     //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
          extensions:'all',
          pName: 'Geolocation',
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                console.log('获取当前位置status',status)
                console.log('获取当前位置result',result)
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  self.$nextTick();
                }
              });
            }
          }
        },
        {
          pName: 'ToolBar',
          events: {
            init(instance) {
              console.log('instance',instance);
            }
          }
        }
      ],
      events: {
        click(e) {
          let { lng, lat } = e.lnglat;
          self.lng = lng;
          self.lat = lat;

          // 这里通过高德 SDK 完成。
          var geocoder = new AMap.Geocoder({
            city: '全国',
            radius: 1000,
            extensions: "all"
          });        
          geocoder.getAddress([lng ,lat], function(status, result) {
            if (status === 'complete' && result.info === 'OK') {
              if (result && result.regeocode) {
                console.log('status',status)
                console.log('result',result)
                // self.address = result.regeocode.addressComponent;
                self.ruleForm.address = result.regeocode.formattedAddress;
                self.$nextTick();
                let districtCode = result.regeocode.addressComponent.adcode;
                let provinceCode = districtCode.substring(0,2) + "0000";
                let provinceName = result.regeocode.addressComponent.province
                self.ruleForm.province = {
                  name:provinceName,
                  code:provinceCode
                }
                self.ruleForm.provinceCode = provinceCode
                self.ruleForm.provinceName = provinceName

                self.getAddressInput(2,provinceCode)
                let cityName = result.regeocode.addressComponent.city
                if(provinceName == "北京市") cityName = '北京城区'
                if(provinceName == "上海市") cityName = '上海城区'
                if(provinceName == "天津市") cityName = '天津城区'
                let cityCode = districtCode.substring(0,4) + "00";
                self.ruleForm.city ={
                  name:cityName,
                  code:cityCode
                }
                console.log('city',self.ruleForm.city)
                self.ruleForm.cityCode = cityCode
                self.ruleForm.cityName = cityName

                self.getAddressInput(3,cityCode)
                self.ruleForm.districtCode = districtCode
                self.ruleForm.districtName = result.regeocode.addressComponent.district
                self.ruleForm.district ={
                  name:result.regeocode.addressComponent.district,
                  code:districtCode
                }
                self.trueMaker = true
                self.markers = [];
                let obj = {
                  position : [lng,lat],
                  text : result.regeocode.formattedAddress,
                  offset: [0, -60],
                }
                self.markers.push(obj)
              }
            }
          });        
        }
      },
      ruleForm:{
        selffetchTimeJson:[],             // 接待时间
        isSelectSelffetchTime:'2',
        moveUpState:1
      },
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
      markers: [
        {
          position: ['1','1'],
          text:'123',
          offset: [0, -60],
        }
      ],
      timerTimes:[],
      timerDays:[],
      selfdays:[
        {
          value:'0',
          label:'周一',
          disabled:false
        },
        {
          value:'1',
          label:'周二',
          disabled:false
        },
        {
          value:'2',
          label:'周三',
          disabled:false
        },
        {
          value:'3',
          label:'周四',
          disabled:false
        },
        {
          value:'4',
          label:'周五',
          disabled:false
        },
        {
          value:'5',
          label:'周六',
          disabled:false
        },
        {
          value:'6',
          label:'周日',
          disabled:false
        }
      ],
      selfcheckDays:[],
      selfeditNow:false
    }
  },
  created(){
    this.getAddressInput(1,'')
  },
  methods:{
    // 添加时间段
    selfaddTimer(){
      this.ruleForm.selffetchTimeJson.push({
        beginTime:'06:00',
        finishTime:'23:00',
        selectDay:[],
        times:['06:00','23:00'],
        type:'edit'
      })
      this.selfeditNow = true
    },
    selfeditTimerData(e,i){
      e.selectDay.forEach(e => this.selfdays[e].disabled = false);
      this.ruleForm.selffetchTimeJson.splice(i, 1)
      this.selfeditNow = false;
    },
    // 接待时间点击确定
    selfgetTimerData(e){
      console.log('e',e)
      if(!e.times || !e.times[0] || !e.times[1]){
        this.$message.error('请选择自提时间');
        return;
      }
      let startDay = Date.parse(`2019-12-31 ${e.times[0]}:00`)
      let endDay = Date.parse(`2019-12-31 ${e.times[1]}:00`)
      if(startDay >= endDay){
        this.$message.error('开始时间应大于结束时间');
        return;
      }
      e.beginTime = e.times[0]
      e.finishTime = e.times[1]
      if(e.selectDay.length == 0){
        this.$message.error('请选择接待日');
        return;
      }
      e.selectDay.forEach(e=>{
        this.selfdays[e].disabled = true
        this.selfcheckDays.push(e)
      });
      e.type = 'finish'
      if(this.selfcheckDays.length != 7){
        this.selfeditNow = false;
      }
    },
    onSubmit(){
      console.log('ruleForm',this.ruleForm)
      if(!this.ruleForm.name){
        this.$message.error('请填写自提点名称');
        return;
      }
      if(!this.ruleForm.phoneNumber){
        this.$message.error('请填写自提点联系电话');
        return;
      }
      if(!this.ruleForm.province || !this.ruleForm.city || !this.ruleForm.district){
        this.$message.error('请选择自提点门店地址');
        return;
      }
      if(!this.ruleForm.address){
        this.$message.error('请填写自提点详细地址');
        return;
      }
      if(!this.trueMaker){
        this.$message.error('请在地图上点击自提点导航位置');
        return;
      }
      this.ruleForm.location = this.markers[0].position.join(',')
      if(this.ruleForm.isSelectSelffetchTime == 1){
        if(this.ruleForm.selffetchTimeJson.length == 0){
          this.$message.error('请添加自提时段');
          return;
        }
        let erHourArr1 = this.ruleForm.selffetchTimeJson.filter(e => e.type == 'edit')
        console.log('erHourArr1',erHourArr1)
        if(erHourArr1.length > 0){
          this.$message.error('请完善自提时段');
          return;
        }
        let time = ''
        if(this.ruleForm.moveUpState == 2){
          if(!this.ruleForm.moveUpDayNum){
            this.$message.error('请完善预约自提的提前时段');
            return;
          }
          this.ruleForm.moveUpNum = this.ruleForm.moveUpDayNum
          time = this.ruleForm.moveUpDayNum
        }
        if(this.ruleForm.moveUpState == 3){
          if(!this.ruleForm.moveUpHourNum){
            this.$message.error('请完善预约自提的提前时段');
            return;
          }
          this.ruleForm.moveUpNum = this.ruleForm.moveUpHourNum
          time = this.ruleForm.moveUpHourNum / 24
        }
        if(!this.ruleForm.maxMoveUpNum){
          this.$message.error('请完善预约天时');
          return;
        }
        if(this.ruleForm.maxMoveUpNum <= time){
          this.$message.error('提前预约时间必须小于最长可预约时间');
          return;
        }
      }
      this.ruleForm.shopId = this.shop.shopId
      this.ruleForm.editor = this.user.userId
      console.log('成功了！',this.ruleForm)
      this.subLoading = true;
      addShopSelffetch(this.ruleForm).then(res=>{
        console.log('res',res)
        if(res.code == 200){
          this.$message({
            message: '创建成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.replace({
              path:'/logistics/selfPickUp'
            })
            this.subLoading = false;
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    backPage(){
      this.$confirm('是否放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({
          path:'/logistics/selfPickUp'
        })
      }).catch(() => {});
    },
    // 地图搜索
    onSearchResult(pois) {
      let self = this;
      console.log('地图搜索结果',pois)
      let latSum = 0;
      let lngSum = 0;
      if (pois.length > 0) {
        pois.forEach(poi => {
          let {lng, lat} = poi;
          lngSum += lng;
          latSum += lat;
          let obj = {
            position:[poi.lng, poi.lat],
            text:poi.name,
            offset: [0, -60],
          }
          this.markers.push(obj);
        });
        let center = {
          lng: lngSum / pois.length,
          lat: latSum / pois.length
        };
        this.center = [center.lng, center.lat];
      }
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      console.log('执行了1',val)
      this.addressList2 = [];
      if(this.ruleForm.city) this.ruleForm.city = '';
      this.addressList3 = [];
      if(this.ruleForm.district) this.ruleForm.district = '';
      this.getAddressInput(2,val.code);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      console.log('执行了2',val)
      this.addressList3 = [];
      if(this.ruleForm.district) this.ruleForm.district = '';
      this.getAddressInput(3,val.code);
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log(`${id1}级下拉获取下拉框地址`,res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .mapBox{
    box-sizing: border-box;
    padding-right: 300px;
    position: relative;
  }
  .amap-demo {
    height: 400px;
  }
  .search-box {
    position: absolute;
    top: 15px;
    left: 80px;
  }
  .timePick{
    margin-top: 10px;
  }
  .timePick /deep/ .el-range-separator{
    line-height: 22px;
  }
  .tag{
    cursor: pointer;
  }
  .li{
    margin-right:20px;
  }
  .mt13{
    margin-top: 13px;
  }
  .mt20{
    margin-top: 20px;
  }
</style>