import request from '@/utils/request'
// 分页查询自提点
export function shopSelffetchPage(data) {
  return request({
    url: `/sys/shopSelffetch/shopSelffetchPage`,
    method: 'post',
    data: data
  })
}
// 店铺是否开启自提
export function getShopSelffetch(id) {
  return request({
    url: `/sys/shopSelffetch/getShopSelffetch?shopId=${id}`,
    method: 'get',
  })
}
// 店铺是否开启自提
export function setShopSelffetch(id,t) {
  return request({
    url: `/sys/shopSelffetch/setShopSelffetch?shopId=${id}&selffedchStatus=${t}`,
    method: 'get',
  })
}
// 添加自提点
export function addShopSelffetch(data) {
  return request({
    url: `/sys/shopSelffetch/addShopSelffetch`,
    method: 'post',
    data: data
  })
}
// 删除自提点
export function deleteShopSelffetch(id,sId) {
  return request({
    url: `/sys/shopSelffetch/deleteShopSelffetch?selffetchId=${id}&shopId=${sId}`,
    method: 'delete',
  })
}
// 查询自提点详情
export function findShopSelffetchById(id) {
  return request({
    url: `/sys/shopSelffetch/findShopSelffetchById?selffetchId=${id}`,
    method: 'get',
  })
}
// 编辑自提点
export function editShopSelffetch(data) {
  return request({
    url: `/sys/shopSelffetch/editShopSelffetch`,
    method: 'post',
    data: data
  })
}